.prices {
    padding: 6% 0;
    background: #833ab4;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    &__title {
        font-size: 2.5vw;
        line-height: 80px;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        color: #f1f1f1;
        text-align: center;
    }
    &__descr {
        font-size: 1.5vw;
        font-weight: 300;
        letter-spacing: 0.025em;
        color: #f1f1f1;
        text-align: center;
    }
    &__card_container {
        padding: 5% 0;
        display: flex;
        justify-content: space-between;
        text-align: center;
    }
    &__card {
        padding: 2% 0;
        width: 30%;
        border: 2px solid black;
        -webkit-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.75);
        box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.75);
        background: #FF4E50;  /* fallback for old browsers */
        background: -webkit-linear-gradient(to bottom, #F9D423, #FF4E50);  /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to bottom, #F9D423, #FF4E50); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        &_title {
            font-size: large;
            border-bottom: 1px solid black;
        }
        &_price {
            font-size: 30px;
            font-weight: 900;
            border-bottom: 1px solid black;
        }
        &_list {
            list-style-type: none;
            padding: 0;
        }
    }
}