@import 'libs/bootstrap-reboot.min';
@import 'libs/font';

* {
    font-family: 'Nexa';
    scroll-behavior: smooth;
}

.container {
    max-width: 80%;
    padding: 0 20px 0 20px;
    margin: 0 auto;
}

.scroll {
    text-align: center;
    position: relative;
    &_text {
        font-size: 18px;
        line-height: 1.2;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        color: #f1f1f1;
        margin-bottom: 10px;
    }
}

.scroll_arrow {
    display: block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 50px;
}

