.about {
    padding: 10% 0;
    // min-height: 1080px;
    background: #22c1c3;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #fdbb2d, #22c1c3);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #fdbb2d, #22c1c3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    &__wrapper {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        margin-bottom: 40px;
    }
    &__img {
        width: 50%;

        img {
            max-width: 70%;
            height: auto;
            -webkit-box-shadow: 0px 0px 40px 0px rgba(250,250,250,1);
            -moz-box-shadow: 0px 0px 40px 0px rgba(250,250,250,1);
            box-shadow: 0px 0px 40px 0px rgba(250,250,250,1);
        }
    }
    &__text {
        width: 40%;
        &_title {
            font-size: 2.5vw;
            font-weight: 300;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            color: #202020;
        }
        &_descr {
            font-size: 1.4vw;
            font-weight: 300;
            letter-spacing: 0.025em;
            color: #202020;
        }
        &_subtitle {
            font-size: 1.2vw;
            line-height: 50px;
            font-weight: bold;
            color: #202020;
        }
    }
    &__scroll {
        .scroll_text {
            color: black;
        }
        .scroll_arrow {}
    }
}