.portfolio{
    background-color: black;
    padding: 10% 0;
    min-height: 1080px;
    &__title {
        text-align: center;
        font-size: 35px;
        font-weight: 300;
        text-transform: uppercase;
        letter-spacing: 0.1em;
        color: #f1f1f1;
    }
    &__descr {
        color: #f1f1f1;
        text-align: center;
        font-size: 1.8vw;
    }
    &__img_wrapper {
        padding: 5% 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__img_item {
        width: 19%;
        height: 150px;
        background: burlywood;
        margin-bottom: 15px;
    }
}